import pickerRegion from "../../components/pickerregion.vue";
var id; 
export default {
	components: {
		pickerRegion
	},
	data:function(){
		return {
			pageLoad:false, 
			pageHide:false,
			pageData:{},
		}
		
	},
	onLoad: function (option) {
		uni.setNavigationBarTitle({
			title: '收货地址编辑'
		});
		id=option.id; 
		this.getPage();
	},

	methods: {
		getPage: function () {
			var that = this;
			uni.request({
				url: that.app.apiHost + "?m=user_address&a=add&ajax=1&id=" + id,
				data: {
					authcode: that.app.getAuthCode(),
					fromapp: that.app.fromapp()
				},
				success: function (data) {
					that.pageLoad = true;
					that.pageData = data.data.data;

				}
			})
		},
		formSubmit: function (e) {
			var that=this;
			uni.request({
				url: that.app.apiHost + "?m=user_address&a=save&ajax=1&fromapp=" + that.app.fromapp() + "&authcode=" + that.app.getAuthCode(),
				method: "POST",
				header: {
					"content-type": "application/x-www-form-urlencoded"
				},
				data: e.detail.value,
				success: function (res) {
					uni.showToast({
						"title":res.data.message
					})
					if(!res.data.error){
						setTimeout(function(){
							uni.navigateBack()
						},600)
						
					}

				}
			})
		}
	},
}
